/* eslint-disable no-alert */
import React, { useState, useContext, useMemo, useEffect } from "react";
import styled from "styled-components";
import { Rnd } from "react-rnd";
import PropTypes from "prop-types";
import { parseUrl } from "query-string";
import AppContext from "../Context/AppContext";
import ImageMemo from "./MemoType/ImageMemo";
import TextMemo from "./MemoType/TextMemo";
import YoutubeMemo from "./MemoType/YoutubeMemo";
import MemoOption from "./MemoOption";

const FACTOR = 120;
const MIN = 1;
const MAX = 30;

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`;

const Content = styled.div`
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #333333;
  background-color: #eeeeee;
`;

const Overlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: ${props => props.bgColor};
  opacity: 0.95;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
`;

const randomColor = () => {
  const colors = ["#F23847", "#95BF8A", "#DAF2C2", "#F2D027", "#F2916D"];
  const idx = Math.floor(Math.random() * colors.length);
  return colors[idx];
};

const Memo = ({ cardData, updateData, removeData }) => {
  const { editMode } = useContext(AppContext);
  /**
   * Problem : onDragStop deltaX, deltaY   (22.3.12 )
   * Solution :   onDrag  position   
   */
  const [position, setPosition] = useState({ x: cardData.x, y: cardData.y });
  const [showOption, setShowOption] = useState(false);
  const color = useMemo(() => randomColor(cardData.id), [cardData.id]);

  let touchT;

  useEffect(() => {
    if (!editMode) {
      setShowOption(false);
    }
  }, [editMode]);

  const handleDrag = (_, data) => {
    if (touchT) {
      clearTimeout(touchT);
    }
    const { deltaX, deltaY } = data;
    const newPosition = { x: position.x + deltaX, y: position.y + deltaY };
    setPosition(newPosition);
  };

  const handleDragStop = () => {
    const { x, y } = position;
    const newData = Object.assign(cardData);
    newData.x = x;
    newData.y = y;
    updateData(newData);
  };

  const handleResizeStop = (_, __, ref) => {
    const width = ref.offsetWidth / 120;
    const height = ref.offsetHeight / 120;
    const newData = Object.assign(cardData);
    newData.width = width;
    newData.height = height;
    updateData(newData);
  };

  const handleDblClickOverlay = () => {
    //  
    if (editMode) {
      setShowOption(v => !v);
    } else {
      //  
    }
  };

  const handleTouchStartOverlay = () => {
    touchT = setTimeout(() => {
      handleDblClickOverlay();
    }, 800);
  };

  const handleTouchEndOverlay = () => {
    if (touchT) {
      clearTimeout(touchT);
    }
  };

  const updateMemo = ({ type, payload }) => {
    const newData = Object.assign(cardData);
    newData.content = {
      type,
      payload,
    };
    updateData(newData);
  };

  const handleOption = option => {
    if (option === "delete") {
      removeData(cardData.id);
    } else if (option === "youtube") {
      /**
       * type 1 : https://www.youtube.com/watch?v=J8TonSFgDk4
       * type 2 : https://youtu.be/waIZiufYDjM
       */
      const url = prompt("   .") || "";
      const parsedUrl = parseUrl(url);
      const newVideoId = parsedUrl.query.v;
      if (newVideoId !== undefined) {
        // type 1
        updateMemo({ type: "youtube", payload: newVideoId });
      } else {
        // type 2
        const splitUrl = url.split("/");
        if (splitUrl.length > 3 && splitUrl[2] === "youtu.be") {
          updateMemo({ type: "youtube", payload: splitUrl[3] });
        }
      }
      setShowOption(false);
    } else if (option === "text") {
      updateMemo({ type: "text", payload: "Something Write Here." });
      setShowOption(false);
    } else if (option === "image") {
      updateMemo({ type: "image", payload: "" });
      setShowOption(false);
    }
  };

  const defaultStyle = {
    x: cardData.x,
    y: cardData.y,
    width: FACTOR * cardData.width,
    height: FACTOR * cardData.height,
  };

  return (
    <Rnd
      default={defaultStyle}
      minWidth={MIN * FACTOR}
      minHeight={MIN * FACTOR}
      maxWidth={MAX * FACTOR}
      maxHeight={MAX * FACTOR}
      resizeGrid={[MIN * FACTOR, MIN * FACTOR]}
      dragGrid={[MIN * FACTOR, MIN * FACTOR]}
      bounds="parent"
      onResizeStop={handleResizeStop}
      onDrag={handleDrag}
      onDragStop={handleDragStop}
      disableDragging={!editMode}
      enableResizing={editMode}
      style={{ zIndex: showOption ? 1000 : 1 }}
    >
      <Wrapper>
        {editMode && (
          <Overlay
            bgColor={color}
            onDoubleClick={handleDblClickOverlay}
            onTouchStart={handleTouchStartOverlay}
            onTouchEnd={handleTouchEndOverlay}
          >
            <span>{cardData.content ? cardData.content.type : "Blank"}</span>
          </Overlay>
        )}
        {showOption && (
          <>
            <MemoOption
              type="image"
              handleOption={handleOption}
              style={{
                width: `${MIN * FACTOR}px`,
                height: `${MIN * FACTOR}px`,
                top: `-${MIN * FACTOR}px`,
                left: `0px`,
              }}
            />
            <MemoOption
              type="youtube"
              handleOption={handleOption}
              style={{
                width: `${MIN * FACTOR}px`,
                height: `${MIN * FACTOR}px`,
                top: `-${MIN * FACTOR}px`,
                left: `${MIN * FACTOR}px`,
              }}
            />
            <MemoOption
              type="text"
              handleOption={handleOption}
              style={{
                width: `${MIN * FACTOR}px`,
                height: `${MIN * FACTOR}px`,
                top: `-${MIN * FACTOR}px`,
                left: `${MIN * FACTOR * 2}px`,
              }}
            />
            <MemoOption
              type="delete"
              handleOption={handleOption}
              style={{
                width: `${MIN * FACTOR}px`,
                height: `${MIN * FACTOR}px`,
                bottom: `-${MIN * FACTOR}px`,
                left: `0px`,
                color: "#ab0000",
              }}
            />
          </>
        )}
        <Content>
          {cardData.content === undefined && (
            <Overlay
              bgColor={color}
              onDoubleClick={handleDblClickOverlay}
              onTouchStart={handleTouchStartOverlay}
              onTouchEnd={handleTouchEndOverlay}
            >
              <span>Blank</span>
            </Overlay>
          )}
          {cardData.content && cardData.content.type === "youtube" && (
            <YoutubeMemo cardData={cardData} />
          )}
          {cardData.content && cardData.content.type === "text" && (
            <TextMemo cardData={cardData} updateMemo={updateMemo} />
          )}
          {cardData.content && cardData.content.type === "image" && (
            <ImageMemo cardData={cardData} updateMemo={updateMemo} />
          )}
        </Content>
      </Wrapper>
    </Rnd>
  );
};

export default Memo;

Memo.propTypes = {
  cardData: PropTypes.shape({
    id: PropTypes.string.isRequired,
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired,
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
    content: PropTypes.shape({
      type: PropTypes.string.isRequired,
      payload: PropTypes.any.isRequired,
    }),
  }).isRequired,
  updateData: PropTypes.func.isRequired,
  removeData: PropTypes.func.isRequired,
};
